<style lang="less" scoped>
	.mh-cell{
		background:#fff;
		margin:5px;
		border-radius: 5px;
		padding:10px;
		display: flex;
		.image{
			width:80px;
			height:80px;
			background-size: cover;
			border-radius:40px;
		}
		.content{
			padding-left:20px;
			.title{
				height:30px;
				line-height:30px;
			}
			.middle{
				height:30px;
				line-height:30px;
			}
			.bottom{
				height:30px;
				line-height:30px;
			}
		}
		
	}
</style>
<template>
	<div class="mh-cell">
		<div :style="{'background-image':`url(${img_src})`}" class="image">
			
		</div>
		<div class="content">
			<div class="title">
				{{title}}
			</div>
			<div class="middle">
				{{middle}}
			</div>
			<div class="bottom">
				{{bottom}}
			</div>
		</div>
		
	</div>
</template>

<script>
	export default {
		name:'mh-cell',
		props:{
			img_src:{
				type:String,
				default:''
			},
			title:{
				type:String,
				default:''
			},
			middle:{
				type:String,
				default:''
			},
			bottom:{
				type:String,
				default:''
			}
		}
	}
</script>

